import React, {createContext, useEffect, useState} from 'react';
import ReactHowler from 'react-howler';
import PropTypes from 'prop-types';
import notification from '../sounds/notification.mp3';

// == constants == //

/**
 * default initial state
 */
const initialState = {
  id: -1,
  state: 0,     // 0: ready to start, 1: working, 2: break, 3: ready to complete
  progress: 0,
};

// == context == //

/**
 * task context
 */
const TaskContext = createContext({
  step: () => {},
  id: -1,
  state: 0,
  progress: 0,
});

// == provider == //

const TaskContextProvider = ({ children }) => {
  const [task, setTask] = useState(initialState);
  const [intervalId, setIntervalId] = useState(-1);
  const [sound, setSound] = useState(false);

  useEffect(() => {
    if ((task.state === 2 || task.state === 3) && !sound) setSound(true);
  }, [task.state]);

  // == private methods == //

  /**
   * starts the timer for the progress bars
   *
   * @param id    id of the task
   * @param state state of the task
   * @param start starting point of the task
   */
  const _loop = (id, state, start) => {
    let progress = start;
    const newIntervalId = setInterval(() => {

      if (progress === 100) {
        clearInterval(newIntervalId);
        step(id, state);

      } else {
        progress += 1;
        setTask({ ...task, id, state, progress });
      }
    }, state === 1 ? 15000 : 3000);
    setIntervalId(newIntervalId);
  };

  // == public methods == //

  /**
   * Steps through the pomodoro method
   *
   * @param id    id of the task
   * @param state state of the task
   * @param start starting point of the task's progress
   */
  const step = (id, state, start = 0) => {
    clearInterval(intervalId);

    if (state === 0) {              // start working on task
      setTask({ id, state: 1, progress: 0 });
      _loop(id, 1, start);
    }

    if (state === 1) {              // start break
      setTask({ ...task, id, state: 2, progress: 0 });
      _loop(id, 2, 0);
    }

    if (state === 2) {              // complete task
      setTask({...task, id, state: 3});
    }

    if (state === 3) {              // reset task
      setTask(initialState);
    }
  };

  // == return == //

  return (
    <TaskContext.Provider value={{
      step,
      id: task.id,
      state: task.state,
      progress: task.progress
    }}>
      <ReactHowler src={notification} preload={true} playing={sound} onEnd={() => setSound(false)} />
      {children}
    </TaskContext.Provider>
  );
};

// == prop-typing == //

TaskContextProvider.propTypes = {
  children: PropTypes.any
};

// == export == //

export default TaskContext;
export {
  TaskContextProvider
};

