import React, {useContext, useEffect, useState} from 'react';
import {Button, Card, Col, Container, Form, Row} from 'react-bootstrap';
import '../styles/pages/Tasks.css';
import AuthContext from '../context/AuthContext';
import {Navigate} from 'react-router';
import {doCreateNote, doRemoveNote, doUpdateNote, getNotes} from '../services/api';
import {Alert, Box, CircularProgress, LinearProgress, Modal, Snackbar, Typography} from '@mui/material';
import Task from '../components/Task';
import TaskContext from '../context/TaskContext';

const Tasks = () => {

  /* context */

  const authContext = useContext(AuthContext);
  const taskContext = useContext(TaskContext);

  authContext.token = authContext.token ? authContext.token : null;
  authContext.verified = authContext.verified ? authContext.verified : null;
  authContext.username = authContext.username ? authContext.username : null;

  /* state */

  const [tasks, setTasks] = useState([]);

  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');

  const [edit, setEdit] = useState(null);
  const [newDescription, setNewDescription] = useState('');
  const [newTitle, setNewTitle] = useState('');

  const [loading, setLoading] = useState(false);
  const [alert, setAlert] = useState('');
  const [snack, setSnack] = useState('');

  /* hooks */

  useEffect(() => {

    getNotes(authContext.token)
      .then(res => {
        setTasks(res.data);
      })
      .catch((e) => {

        if (e.response.status === 401 && authContext.token) {
          setAlert('You session has expired');
          authContext.logout();
          return;
        }
        setAlert('Something went wrong while fetching your tasks');
      });
  }, []);

  /* handlers */

  const startTask = (id) => {
    if (taskContext.id !== id) taskContext.step(id, 0, taskContext.progress);
    else taskContext.step(taskContext.id, taskContext.state);
  };

  const createTask = async () => {

    if (!title) {
      setAlert('Please enter a title');
      setTimeout(() => setAlert(''), 5000);
      return;
    }

    setLoading(true);

    try {
      const res = await doCreateNote(authContext.token, title, description ? description : null);
      setTasks(tasks.concat({
        id: res.data.id,
        title: res.data.title,
        description: res.data.description,
      }));
      setTitle('');
      setDescription('');
      setSnack('Task created');

    } catch (e) {
      console.log(e);
      setAlert('Something went wrong while creating a new task');
      setTimeout(() => setAlert(''), 5000);
    }
    setLoading(false);
    setEdit(null);
  };

  const updateTask = async () => {

    if (!newTitle) {
      setAlert('Please enter a valid title');
      setEdit(null);
      setTimeout(() => setAlert(''), 5000);
      return;
    }

    setLoading(true);

    try {
      await doUpdateNote(authContext.token, edit, newTitle, newDescription);
      setTasks(tasks.map(t => {
        if (t.id !== edit) return t;
        return {
          ...t,
          title: newTitle,
          description: newDescription
        };
      }));
      setSnack('Task updated');

    } catch (e) {
      console.log(e);
      setAlert('Something went wrong while updating your task');
      setTimeout(() => setAlert(''), 5000);
    }
    setEdit(false);
    setLoading(false);
  };

  const removeTask = async (id) => {

    try {
      await doRemoveNote(authContext.token, id);
      setTasks(tasks.filter((task) => task.id !== id));
      setSnack('Task removed');

    } catch (e) {
      console.log(e.error);
      setAlert('Something went wrong while deleting the task');
      setTimeout(() => setAlert(''), 5000);
    }

  };

  const currentTaskTitle = (id) => {

    for (const task of tasks) {
      if (task.id === id) {
        return ` (${task.title})`;
      }
    }
  };

  /* redirect if not authenticated */

  if (!authContext.token) {
    return (
      <Navigate to={'/login'} />
    );
  }

  if (authContext.token && !authContext.verified) {
    return (
      <Navigate to={'/verify'} />
    );
  }

  /* return tasks */

  return (
    <div>

      <Row>

        <Col/>

        <Col xs={'auto'}>

          {
            tasks.map((task, i) => (
              <Task
                key={i}
                startHandler={startTask}
                deleteHandler={removeTask}
                editHandler={(id, tit, desc) => {
                  setEdit(id);
                  setNewTitle(tit);
                  setNewDescription(desc);
                }}
                id={task.id}
                title={task.title}
                description={task.description}
                progress={
                  task.id === taskContext.id
                  && taskContext.state === 1 ? taskContext.progress : 100
                }
              />
            ))
          }

          <Card id={'create'} >

            <Container id={'container'}>

              <Form>

                <h4>
                  Create a new task!
                </h4>

                <Form.Group controlId={'title'} id={'in'}>
                  <Form.Control
                    placeholder={'Enter a title'}
                    onChange={(e) => setTitle(e.target.value)}
                    value={title}
                  />
                </Form.Group>

                <Form.Group controlId="description" id={'in'}>
                  <Form.Control
                    placeholder={'Enter a description (optional)'}
                    onChange={(e) => setDescription(e.target.value)}
                    as='textarea'
                    style={{ height: '10rem' }}
                    value={description}
                  />
                </Form.Group>

                <div id={'buttonHolder'}>

                  <Button id={'button'} variant="danger" onClick={createTask}>
                    Create
                  </Button>

                  {loading && <CircularProgress color={'inherit'}/>}

                </div>

              </Form>

            </Container>

          </Card>

          <div id={'alertDiv'}>

            {alert && <Alert
              id={'alert'}
              open={alert && true}
              onClose={() => setAlert('')}
              severity={'error'}
              sx={{
                borderRadius: '1rem',
                marginTop: '5px',
                maxWidth: '100%'
              }}
            >{alert}</Alert>}

          </div>

        </Col>

        <Col/>

      </Row>

      <Modal
        open={taskContext.state === 2}
        onClose={() => {
          taskContext.step(taskContext.id, taskContext.state);
        }}
      >
        <Box sx={{
          borderRadius: '1rem',
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: 'min(600px, 80%)',
          bgcolor: 'background.paper',
          border: 'none',
          boxShadow: 24,
          p: 4,
        }}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Time to take a break!
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            {'You have been working hard; take some to chill and recharge. We will notify you when it\'s time to get back to work.'}
          </Typography>

          <br/>

          <div id={'breakBar'}>
            <LinearProgress variant={'determinate'} color={'inherit'} value={taskContext.progress} />
          </div>

          <br/>

          <div>

            <Button variant={'danger'} id={'button'} onClick={() => {
              taskContext.step(taskContext.id, taskContext.state);
            }}
            >Skip Break</Button>

          </div>

        </Box>
      </Modal>

      <Modal
        open={taskContext.state === 3}
        onClose={() => {
          taskContext.step(taskContext.id, taskContext.state);
        }}
      >
        <Box sx={{
          borderRadius: '1rem',
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: 'min(600px, 80%)',
          bgcolor: 'background.paper',
          border: 'none',
          boxShadow: 24,
          p: 4,
        }}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Did you complete the task?
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            {`Did you complete the task you have been working on${currentTaskTitle(taskContext.id)}?`}
          </Typography>

          <br/>

          <Button
            style={{
              marginBottom: '.5rem'
            }}
            variant={'danger'}
            id={'button'}
            onClick={() => removeTask(taskContext.id).then(() => taskContext.step(taskContext.id, taskContext.state, 0))}
          >
            Yes! Go ahead and remove it.
          </Button>

          <Button
            variant={'danger'}
            id={'button'}
            onClick={() => taskContext.step(taskContext.id, taskContext.state, 0)}
          >
            No! Keep it there.</Button>

        </Box>
      </Modal>

      <Modal
        open={!!edit}
        onClose={() => () => setEdit(null)}
      >
        <Box sx={{
          borderRadius: '1rem',
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: 'min(600px, 80%)',
          bgcolor: 'background.paper',
          border: 'none',
          boxShadow: 24,
          p: 4,
        }}>

          <div id={'edit'}>

            <h4>
               Edit your task: {currentTaskTitle(edit)}
            </h4>

            <Form.Group controlId={'title'} id={'in'}>
              <Form.Control
                placeholder={'Enter new a title'}
                onChange={(e) => setNewTitle(e.target.value)}
                value={newTitle}
              />
            </Form.Group>

            <Form.Group controlId="description" id={'in'}>
              <Form.Control
                placeholder={'Enter a new description (optional)'}
                onChange={(e) => setNewDescription(e.target.value)}
                as='textarea'
                style={{ height: '10rem' }}
                value={newDescription}
              />
            </Form.Group>

            <div id={'buttonHolder'}>

              <Button
                style={{
                  marginBottom: '.5rem'
                }}
                id={'button'}
                variant={'danger'}
                onClick={updateTask}>
                  Update
              </Button>

              <Button
                style={{
                  marginBottom: '.5rem'
                }}
                id={'button'}
                variant={'danger'}
                onClick={() => setEdit(false)}>
              Cancel
              </Button>

              {loading && <CircularProgress color={'inherit'}/>}

            </div>

          </div>


        </Box>

      </Modal>

      <Snackbar
        open={!!snack}
        autoHideDuration={5000}
        onClose={() => setSnack('')}
        message={snack}
      />

    </div>
  );
};

export default Tasks;